.WelcomeScreen {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  padding-top: 15%;
  text-align: center;
  box-sizing: border-box;
  background: #394850;
  background-image: url("/src/img/phone-1869510_1920.jpg"); /* Image by <a href="https://pixabay.com/users/pexels-2286921/?utm_source=link-attribution&amp;utm_medium=referral&amp;utm_campaign=image&amp;utm_content=1869510">Pexels</a> from <a href="https://pixabay.com/?utm_source=link-attribution&amp;utm_medium=referral&amp;utm_campaign=image&amp;utm_content=1869510">Pixabay</a> */
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  }

  .google-btn {
    width: 184px;
    height: 42px;
    margin: 1rem;
    background-color: #4285f4;
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
    }
  .google-btn:hover {
    box-shadow: 0 0 6px #4285f4;
    }
  .google-btn:active {
      background: #1669f2;
      }
  .google-icon-wrapper {
      position: absolute;
      margin-top: 1px;
      margin-left: 1px;
      width: 40px;
      height: 40px;
      border-radius: 2px;
      background-color: #fff;
    }
    .google-icon {
      position: absolute;
      margin-top: 11px;
      margin-left: -8px;
      width: 18px;
      height: 18px;
    }
  .btn-text {
    float: right;
    color: #fff;
    font-size: 14px;
    letter-spacing: 0.2px;
    font-family: "Roboto";
    background-color: #4285f4;
    width: 80%;
    height: 100%;
    text-align: center;
    margin-top: 0;
    margin-right: -5px;
  }

.privacy-policy-link {
  width: 184px;
  height: 42px;
  margin: 1rem;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
  color: mintcream
  }
  